<!-- 聊天 -->
<template>
  <div class="chat">
    <div class="top">
      <div class="back" @click="back"></div>
      {{chatTitle}}
    </div>
    <img v-if="!isShowContent" class="loading" src="../../assets/images/loading.gif" alt="">
    <div class="content" ref="content">
      <div class="list-item" :class="[isShowContent ? '' : 'hidden']" v-for="(item, index) in chatList" :key="index">
        <div class="msg-item-right msg-item" v-if="item.aamid">
          <h5>{{item.time || item.sendtime | onlyShowTime}}</h5>
          <div class="main">
            <div class="avatar">
              <img src="../../assets/images/user@2x.png" alt="">
            </div>
            <div class="msg-content">
              <p>{{item.text}}</p>
            </div>
          </div>
        </div>
        <div class="msg-item-left msg-item" v-else>
          <h5>{{item.time || item.sendtime | onlyShowTime}}</h5>
          <div class="main">
            <div class="avatar">
              <img :src="moduleIcon" alt="">
            </div>
            <div class="msg-content">
              <h6>{{item.text_head ? item.text_head : '———— 系 统 消 息  ————'}}</h6>
              <p>{{item.text}}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="input-bar">
      <!-- <div class="textarea" ref="textarea" contenteditable="true"></div> -->
      <input type="text" v-model="inputVal" placeholder="请键入您对该视图的建议">
      <div class="submit" @click="submit">发送</div>
      <!-- <input type="text" placeholder="请键入您对该视图的建议"> -->
    </div>
  </div>
</template>

<script>
import moment from 'moment';
import axios from 'axios';
const productionUrl = window.origin + '/mobile456/xxtx/py';
const instance = axios.create({
  baseURL: process.env.NODE_ENV === 'development' ? '/api/mobile' : productionUrl
});
export default {
  name: "MessageChat",
  data() {
    return {
      chatTitle: '通知消息',
      inputVal: '',
      socket: null,
      chatList: [],
      moduleIcon: '',
      isShowContent: false
    };
  },
  mounted() {
    const { modulename, icon, moduleid } = this.$route.params;
    this.chatTitle = modulename || '通知消息';
    this.moduleIcon = icon;
    this.getChatList();
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    // 滚动到最底部
    scrollToBottom() {
      setTimeout(() => {
        window.scrollTo({
          top: this.$refs.content.scrollHeight
        });
        this.isShowContent = true;
      });
    },
    // 定位到关键词位置
    scrollToKeyWord() {
      let index = 0;
      this.chatList.forEach((item, i) => {
        const text = item.text;
        if (text && text.indexOf(this.$route.params.kw) !== -1) {
          index = i;
        }
      });
      this.$nextTick(() => {
        const currentItem = this.$refs.content.children[index];
        setTimeout(() => {
          window.scrollTo({
            top: currentItem.offsetTop - 60
          });
          this.isShowContent = true;
        }, 200);
      });
    },
    // 请求历史聊天数据
    getChatList() {
      const { moduleid, modulename } = this.$route.params;
      const aamid = this.$store.state.userInfoObj.aamid;
      instance.get('/chat-page', {
          params: { aamid, moduleid, modulename }
        }).then(res => {
        	if (res.status === 200 && res.data.message === 'OK') {
            this.chatList = res.data.data || [];
            if (this.$route.params.kw) {
              this.scrollToKeyWord();
            } else {
              this.scrollToBottom();
            }
        	}
        }).catch(err => {});
    },
    // 发送数据
    sendSocketData(text) {
      if (text === '') return;
      const { moduleid, modulename } = this.$route.params;
      const aamid = this.$store.state.userInfoObj.aamid;
      this.chatList.push({
        text,
        aamid,
        time: moment().format("YYYY-MM-DD HH:mm")
      });
      instance.post("/reply-msg",{
        aamid: this.$store.state.userInfoObj.aamid,
        moduleid,
        modulename,
        replymess: text,
        replytime: moment().format("YYYY-MM-DD HH:mm:ss.SSS")
      }).then(res => {
        if (res.status === 200 && res.data.message === 'OK') {
          this.chatList.push({
            text_head: res.data.data.text_head,
            moduleid,
            text: res.data.data.text,
            time: moment().format("YYYY-MM-DD HH:mm")
          });
          this.scrollToBottom();
        }
      });
      this.scrollToBottom();
    },
    submit() {
      const value = this.inputVal.trim();
      if (value) {
        this.inputVal = '';
        this.sendSocketData(value);
      }
    }
  },
  filters: {
    onlyShowTime(value) {
      const today = moment().format("YYYY-MM-DD");
      return value.indexOf(today) !== -1 ? value.substring(11, 16) : value.substring(0, 16);
    }
  },
  beforeDestroy() {
    
  }
};
</script>
<style lang='less' scoped>
/* @import url(); 引入css类 */
.chat{
  height: 100%;
}
