  position: fixed;
  width: 0.4rem;
  height: 0.4rem;
  top: 0.6rem;
  left: 50%;
  margin-left: -0.2rem;
}
.content{
  padding: 0.5rem 0 0.6rem;
  min-height: 100%;
  background: #eeeeee;
  box-sizing: border-box;
  .hidden{
    visibility: hidden;
  }
  .msg-item{
    overflow: hidden;
    margin-top: 0.2rem;
    h5{
      font-size: 0.14rem;
      text-align: center;
      color: #A6A6A6;
      margin-bottom: 0.16rem;
    }
    .main{
      overflow: hidden;
      .avatar{
        float: left;
