        justify-content: center;
        align-items: center;
        border-radius: 4px;
        overflow: hidden;
        img{
          width: 100%;
        }
      }
      .msg-content{
        float: left;
        max-width: 2.5rem;
        margin: 0 0.1rem;
        padding: 0.1rem 0.12rem;
        font-size: 0.16rem;
        color: #3A3A3A;
        border-radius: 4px;
        line-height: 0.22rem;
        box-sizing: border-box;
        h6{
          text-align: center;
        }
      }
    }
  }
  .msg-item-left{
    .main{
      padding-left: 0.12rem;
    }
    .msg-content{
      background: #fff;
    }
  }
  .msg-item-right{
    .main{
      padding-right: 0.12rem;
      .avatar{
        float: right;
        img{
          height: 90%;
          width: auto;
        }
      }
      .msg-content{
        float: right;
        background: #66DAD6;
      }
    }
  }
}
.input-bar{
  min-height: 0.5rem;
  width: 100%;
  position: fixed;
  bottom: 0;
  background: #F1F1F1;
  box-shadow: 0 0 4px 0 rgba(0,0,0,0.11);
  padding: 0.07rem 0.1rem;
  box-sizing: border-box;
  input{
    float: left;
    height: 0.36rem;
    line-height: 0.2rem;
    font-size: 0.14rem;
    padding: 0.08rem 0.15rem;
    padding-right: 0.42rem;
    border-radius: 0.18rem;
    background: #fff url('../../assets/images/icon_keyboard@2x.png') no-repeat 2.6rem 0.02rem;
    background-size: 0.32rem 0.32rem;
    width: 3rem;
    box-sizing: border-box;
    -webkit-user-select: text
  }
  // .textarea{
  //   float: left;
  //   outline: none;
  //   min-height: 0.2rem;
  //   line-height: 0.2rem;
  //   font-size: 0.14rem;
  //   padding: 0.08rem 0.15rem;
  //   padding-right: 0.42rem;
  //   border-radius: 0.18rem;
  //   background: #fff url('./assets/images/icon_keyboard@2x.png') no-repeat 2.6rem 0.02rem;
  //   background-size: 0.32rem 0.32rem;
  //   width: 3rem;
  //   box-sizing: border-box;
  //   -webkit-user-select:text
  // }
  // .textarea:empty:before{ 
  //   content: '请键入您对该视图的建议'; 
  //   color: gray; 
  // } 
  // .textarea:focus:before{
  //   content:none;
  // }
  .submit{
    float: right;
    margin-top: 0.04rem;
    width: 0.5rem;
    height: 0.28rem;
    font-size: 0.14rem;
    color: #ffffff;
    background: #45a4b1;
    line-height: 0.28rem;
    text-align: center;
  }
}
</style>